import React, { Component } from 'react';
import cat from '../jpg/cat.jpg';
import mockingjay from '../jpg/mockingjay.jpg';

class Zadanie2 extends Component{
    constructor(props) {
        super(props);
        this.state = {
            bool: false,
        };
    }

    onSwitchChange = (e) => {
        this.setState({
            bool: e.target.checked
        });
    }

    picture = () => {
        if (this.state.bool) {
            return(<img className="featurette-image img-fluid mx-auto" alt="React"  src={cat}></img>);
        } else {
            return(<img className="featurette-image img-fluid mx-auto" alt="React"  src={mockingjay}></img>);
        }
    }

    render() {
        return(
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-md-8">
                        <div class="card border-secondary mb-3">
                        <div class="card-header">{this.props.title}</div>
                        <div class="card-body">
                            <p class="card-text">Utwórz komponent przycisku oraz funkcję, która zostanie wywołana po jego kliknięciu.
                            Przycisk powinien zmieniać zmienną boolowską na zasadzie toggle.
                            Zawartość strony powinna być zależna od wartości tej zmiennej (np. wyświetlać jakiś element tylko gdy zmienna ma wartość true).
                            Komponent przycisku musi być wyświetlany niezależnie od wartości zmiennej.</p>
                        </div>
                    </div>
                    </div>
                </div>
                <div className="row justify-content-center">
                    <div className="form-group">
                        <div className="custom-control custom-switch">
                            <input type="checkbox" className="custom-control-input" disabled="" id="customSwitch2" onChange={this.onSwitchChange}></input>
                            <label className="custom-control-label" for="customSwitch2">Switch element</label>
                        </div>
                    </div>     
                </div>
                {this.picture()}
            </div>
        );
    }
}

export default Zadanie2;