import React, { Component } from 'react';
import { loremIpsum } from "lorem-ipsum";

const list = [];

function ListItem(params) {
    return (<li className="list-group-item">{params.number}. {params.value}</li>);
}

function GenerateList() {
    list.length = 0
    const amount = Math.floor((Math.random() * 30) + 15)
    for (var i = 0; i <= amount; i++) {
        list.push(i);
    }
    const listItems = list.map((num, index) => 
        <ListItem key={index} value={loremIpsum()} number={num}/>
    );
    return (<ul className="list-group List-items-to-left">{listItems}</ul>);
}

class Zadanie3 extends Component {
    render() {
        return (
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-md-8">
                        <div className="card border-secondary mb-3">
                            <div className="card-header">{this.props.title}</div>
                            <div className="card-body">
                                <p className="card-text">Utwórz nowy komponent w którym wylistujesz elementy znajdujące się w przygotowanej przez siebie tablicy (o dowolnej zawartości).</p>
                                <ul className="noMargin List-items-to-left">
                                    <li>Reprezentacja graficzna powinna być taka sama dla wszystkich elementów tablicy.</li>
                                    <li>Implementacja ma być niezależna od liczby elementów kolekcji.</li>
                                    <li>Należy zadbać o unikalny klucz (właściwość key) dla każdego listowanego elementu (zobacz poniższy link do dokumentacji).</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row justify-content-center">
                    <div className="col-md-8">
                        <GenerateList />
                    </div>                 
                </div>
            </div>
        );
    }
}

export default Zadanie3;