import React, { Component } from 'react';
import logoReact from '../png/react.png'
import logoJS from '../png/js.png'

class HomePage extends Component{

    render() {
        return(
            <div className="container marketing TextAlign">
                <h1>{this.props.title}</h1>
                <hr className="featurette-divider"></hr>
                <div className="row featurette">
                    <div className="col-md-7">
                        <h2>React.js</h2>
                        <hr></hr>
                        Tematem tego laboratorium będzie zapoznanie się z biblioteką React.js.
                        Wszelkie materiały wraz z poradnikami znajdują się na stronie oficjalnej dokumentacji.<br></br><hr></hr><br></br> 
                        <b>Przygotowanie:</b><br></br>          
                        Podczas rozwiązywania poniższych zadań można wykorzystać menadżer pakietów npm (patrz: wersja zalecana), jednak możliwe jest zastosowanie bibliotek ręcznie (patrz: wersja alternatywna).
                        Uwaga: należy wybrać dokładnie jedną wersję przygotowania środowiska.<br></br><hr></hr><br></br>
                        <b>Wersja zalecana:</b><br></br> 
                        Zainstaluj menadżer pakietów npm korzystając instalatora Node.js: Pobierz Node.js 
                        oraz zapoznaj się z instrukcją utworzenia szablonowej aplikacji z użyciem biblioteki React: Instrukcja tworzenia aplikacji React <br></br><hr></hr><br></br> 
                        <b>Wersja alternatywna:</b><br></br> 
                        Ta wersja obejmuje zastosowanie samodzielnie wybranych narzędzi.
                        Adresy URL do kodu źródłowego modułów biblioteki React niezbędnych do wykonania tej instrukcji.<br></br><hr></hr><br></br>
                        <ul>
                            <li>https://unpkg.com/react@16.0.0/umd/react.production.min.js</li>
                            <li>https://unpkg.com/react-dom@16.0.0/umd/react-dom.production.min.js</li>
                            <li>https://unpkg.com/react-router-dom/umd/react-router-dom.min.js</li>
                        </ul>
                        <br></br> <hr></hr>
                        <b>Forma oddania:</b><br></br> 
                        <ul>
                            <li>plik zip z kodem strony</li>
                            <li>link do dostępnej publicznie strony z rozwiązaniem zadań lub docker do uruchomienia na komputerze prowadzącego (tak jak w zadaniu poprzednim)</li>
                            <li>wysłany do prowadzącego mailem z tytułem zawierającym [PIW] i grupę zajęć np. [11/P]</li>
                            <li>termin (do 12 dni po terminie zajęć):  30.05/6.06  (zależy od parzystości grupy: N lub P)</li>
                        </ul>
                    </div>
                    <div className="col-md-5">
                        <div className="row">
                            <img className="featurette-image img-fluid mx-auto" alt="React"  src={logoReact}></img>
                        </div>
                        <div className="row">
                            <img className="featurette-image img-fluid mx-auto" alt="React"  src={logoJS}></img>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default HomePage;